import Dexie from 'dexie';

export const db = new Dexie('biometrex');
db.version(1).stores({
    subjects: '++id, key',
});

export async function countSubjects(){
    return  await db.subjects
        .count();
}

export async function deleteAllSubjects(){
    await db.subjects
        .clear();

    console.log('delete existing')
}

export async function getAllSubjects(){
    return db.table('subjects').toArray();
}

export async function saveSubjectDetection(key, faceDescription){
    const exist =  await db.subjects
        .where("key")
        .equals(key)
        .first();

    if(!exist){
        console.warn('try to set face data to unexisted record')
        return;
    }

    console.log('save face for subject', faceDescription)
    await db.subjects.update(exist.id, {
        faceDescription: exist.faceDescription.concat(faceDescription)
    });

}

export async function initSubjectRow(key, personFiles, strategy ){
    console.log('init for ', key, strategy)
    const exist =  await db.subjects
        .where("key")
        .equals(key)
        .first();

    const newFaceImage = personFiles.map( file => new Blob([file],{type: file.type}));

    if(exist && strategy !== 'override'){
        console.log('exist, strategy', strategy, exist);

        //todo unique same image
        await db.subjects.update(exist.id, {
            personFiles: exist.personFiles.concat(newFaceImage)
        })
    }
    else{
        if(exist){
            console.log('exist, strategy', strategy, exist)
            await db.subjects
                .where("key")
                .equals(key)
                .delete();

            console.log('delete existing')
        }

        console.log('no exist, strategy')
        await db.subjects.add({
            key: key,
            personFiles: newFaceImage,
            faceDescription: []
        });

        console.log('added')
    }
}