<script>
import $ from "jquery";
import * as faceAPI from 'face-api.js'
import {countSubjects, deleteAllSubjects, initSubjectRow, saveSubjectDetection} from "@/services/database/db";

const Queue = require('simple-promise-queue');

const MODEL_URL = `/models`
export default {
  name: "TrainAttachmentComponent",
  data() {
    return {
      $directory: '',
      eventInit: false,
      traning: false,
      concurrency: 1,
      trainProgress: 0,
      willTrainCount: 0,
      idSize: 36,
      subjectCount: '',
      strategySelectValue: {
        text: 'Écraser',
        value: 'override',
      },
      strategyOptions: [
        {
          text: 'Écraser',
          value: 'override',
        },
          {
          text: 'Fusionner',
          value: 'merge',
        },

      ]
    };
  },
  components: {},

  setup() {

    return {}
  },
  mounted() {
    this.iniFaceJs();
    this.countSubject();
  },

  methods: {
    trainPercent() {
      return this.trainProgress * 100 / this.willTrainCount
    },
    async reset() {
      this.traning = false;
      this.trainProgress = 0;
      this.willTrainCount = 0;
      this.countSubject();
    },
    async sleep(ms) {
      return new Promise(resolve => setTimeout(resolve, ms));
    },
    async countSubject() {
      this.subjectCount = await countSubjects();
    },
    async iniFaceJs() {
      await faceAPI.loadSsdMobilenetv1Model(MODEL_URL)
      await faceAPI.loadFaceLandmarkModel(MODEL_URL)
      await faceAPI.loadFaceRecognitionModel(MODEL_URL)
    },


    selectFolder() {

      if (this.traning) {
        return this.$vaToast.init('Une tache est deja en cours');
      }

      const $directory = $(this.$refs.input);
      if (!this.eventInit) {
        this.listenChangeEvent($directory)
        this.eventInit = true;
      }
      $directory.click();

    },
    groupFilePerId(files) {
      const helper = {};

      for (let file of files) {
        const key = file.name.substring(0, this.idSize);
        helper[key] = helper[key] || []

        //todo only images
        helper[key].push(file);
      }

      return helper;
    },
    listenChangeEvent($directory) {
      $directory.on('change', () => {
        const fileList = $directory[0].files;
        this.willTrainCount = fileList.length;
        console.log(this.willTrainCount, 'willTrainCount')
        const groupFileList = this.groupFilePerId(fileList);
        this.trainData(groupFileList)

      })
    },
    async trainSingleFace(key, pFile, resolve) {
      // todo send it to worker
      try {
        const img = await faceAPI.bufferToImage(pFile)
        const fullFaceDescription = await faceAPI.detectSingleFace(img).withFaceLandmarks().withFaceDescriptor()

        if (!fullFaceDescription) {
          console.log(`no faces detected for in image ${pFile.name}`)
          this.trainProgress++;
          resolve();
          return;
        }

        //const faceDescriptors = [fullFaceDescription.descriptor];
        //const detection = new faceAPI.LabeledFaceDescriptors(key, faceDescriptors) ;
        const detection = fullFaceDescription ;

        console.log(this.trainProgress, this.willTrainCount)
        //await this.sleep(1000);//todo rm
        /*await*/
        saveSubjectDetection(key, detection);
        this.trainProgress++;
        resolve();
        return detection;
      } catch (e) {
        console.log(e);
        resolve()
      }
    },
    async trainData(groupFileList) {

      if(this.strategySelectValue.value === 'override'){
        await deleteAllSubjects();
      }

      const queue = new Queue({
        autoStart: true,
        concurrency: this.concurrency
      });


      this.traning = true;
      console.log('start training');
      this.$vaToast.init('Entrainement sur le point de démarrer');

      const promisesArray = [];

      for (let key of Object.keys(groupFileList)) {
        promisesArray.push(queue.pushTask(async (resolve) => {
          try {
            const personFiles = groupFileList[key];
            await initSubjectRow(key, personFiles, this.strategySelectValue.value);
            for (let pFile of personFiles) {
              promisesArray.push(this.trainSingleFace(key, pFile, resolve));
            }
          } catch (e) {
            console.log(e);
            resolve();
          }
        }));

      }


      const resultsTrain = (await Promise.all(promisesArray)).filter(a => a);
      this.traning = false;
      queue.stop();
      this.reset();
      console.log(resultsTrain, 'resultsTrain');

      this.$vaToast.init('Tache terminée');


    }

  },
  computed: {}
};
</script>

