<script>
import $ from "jquery";
import * as faceAPI from 'face-api.js'
import {countSubjects, getAllSubjects} from "@/services/database/db";

const Queue = require('simple-promise-queue');

const MODEL_URL = `/models`
export default {
  name: "SearchAttachmentComponent",
  data() {
    return {
      $directory: '',
      eventInit: false,
      searching: false,
      searchBlobUrl: null,
      concurrency: 1,
      searchProgress: 0,
      willTrainCount: 0,
      threshold: 80,
      maxResult: 10,
      subjectCount: '',
      matches: [],
      queue: null,

    };
  },
  components: {},

  setup() {

    return {}
  },
  mounted() {
    this.iniFaceJs();
    this.countSubject();
  },

  methods: {
    async stopSearch(){
      console.log('here')
      this.queue?.stop();
      await this.sleep(100);
      this.reset();
    },
    searchPercent() {
      return this.searchProgress * 100 / this.willTrainCount
    },
    async reset() {
      this.searching = false;
      this.matches = [];
      this.queue = null;
      this.searchProgress = 0;
      this.willTrainCount = 0;
      this.countSubject();
    },
    openImage(data){
      window.open(data, '_blank', "toolbar=1, scrollbars=1, resizable=1, width=" + 1015 + ", height=" + 800)
    },
    async sleep(ms) {
      return new Promise(resolve => setTimeout(resolve, ms));
    },
    async countSubject() {
      this.subjectCount = await countSubjects();
    },
    async iniFaceJs() {

      await faceAPI.loadSsdMobilenetv1Model(MODEL_URL)
      await faceAPI.loadFaceLandmarkModel(MODEL_URL)
      await faceAPI.loadFaceRecognitionModel(MODEL_URL)
    },


    selectFolder() {

      if (this.searching) {
        return;
        //return this.$vaToast.init('Une tache est deja en cours');
      }

      const $directory = $(this.$refs.input);
      if (!this.eventInit) {
        this.listenChangeEvent($directory)
        this.eventInit = true;
      }
      $directory.click();

    },

    listenChangeEvent($directory) {
      $directory.on('change', () => {
        const canditatFile = $directory[0].files[0];
        console.log(canditatFile)
        this.willTrainCount = this.subjectCount;
        console.log(this.willTrainCount, 'willTrainCount')
        this.searchFace(canditatFile)

      })
    },

    finishSearch(){
      this.$vaToast.init('Recherche terminé');
      this.queue?.stop();
      console.log(this.matches)
    },
    getSubjectPhoto(subject){
      try {
        return URL.createObjectURL(subject.personFiles[0])
      } catch (e) {
        console.log()
      }
    },
    async matchPerson(subject, candidatDescription, resolve){

      try {
        for (let descriptor of subject.faceDescription) {
          let distance = faceAPI.euclideanDistance(descriptor.descriptor, candidatDescription.descriptor);

          const score = (1 - distance) * 100;
          if (score >= this.threshold) {
            subject.score = score;
            this.matches.push(subject);
            break;
          }
        }


      } catch (e) {
        console.log(e);
      }

      this.searchProgress++;
      resolve();

      if(this.matches.length >= this.maxResult){
        this.finishSearch();
      }
    },

    async searchFace(canditatFile) {
       this.queue = new Queue({
        autoStart: true,
        concurrency: this.concurrency
      });


       this.searchBlobUrl = URL.createObjectURL(new Blob([canditatFile],{type: canditatFile.type}))

      this.searching = true;
      this.$vaToast.init('Recherche sur le point de démarrer');
      console.log('start searching')

      const img = await faceAPI.bufferToImage(canditatFile)
      const candidatDescription = await faceAPI.detectSingleFace(img).withFaceLandmarks().withFaceDescriptor()
      const promisesArray = [];
      const allSubject = await getAllSubjects();

      for(let subject of allSubject){
        promisesArray.push(this.queue.pushTask((resolve) => {
          this.matchPerson(subject, candidatDescription, resolve);
        }));
      }

      await Promise.all(promisesArray);
      this.finishSearch();


    }

  },
  computed: {}
};
</script>

